@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-bold.woff2") format("woff2"), url("../fonts/alumnisans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-extrabold.woff2") format("woff2"), url("../fonts/alumnisans-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-semibold.woff2") format("woff2"), url("../fonts/alumnisans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-light.woff2") format("woff2"), url("../fonts/robotocondensed-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #102654;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  margin: 0 auto;
  padding: 0 45px;
  width: 100%;
  max-width: 1250px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
    max-width: 100%;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #0039db;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #2e2e2e;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(16, 38, 84, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(16, 38, 84, 0.5);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  padding: 33px 0 32px;
  min-height: 92px;
  z-index: 10;
  transition: all 200ms ease;
}
@media (max-width: 1199px) {
  .header {
    padding: 36px 0 30px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 15px 0;
    min-height: 55px;
  }
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo {
  transform: translateY(5px);
}
@media (max-width: 1199px) {
  .header__logo {
    width: 82px;
    height: auto;
    transform: none;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 69px;
    height: auto;
  }
}

.header__phone {
  margin-left: auto;
}

.header__phone a {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 1;
  letter-spacing: 0.03em;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header__phone a {
    font-weight: 700;
    font-size: 24px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header__phone a {
    font-weight: 600;
    font-size: 20px;
  }
}

.hero {
  padding: 92px 0 22px;
  margin-top: -92px;
  position: relative;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}
@media (max-width: 1199px) {
  .hero {
    padding: 92px 0 72px;
  }
}
@media (max-width: 767px) {
  .hero {
    padding: 55px 0 79px;
    margin-top: -55px;
    min-height: 491px;
  }
}

.hero__container {
  padding: 0 65px;
  display: grid;
  grid-template-columns: 377px auto;
  gap: 64px;
}
@media (max-width: 1199px) {
  .hero__container {
    padding: 0 45px;
    grid-template-columns: repeat(2, auto);
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .hero__container {
    display: block;
    gap: 0;
  }
}

.hero__content {
  display: grid;
  grid-template-columns: 115px 245px;
  gap: 16px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .hero__content {
    grid-template-columns: 115px 188px;
    gap: 20px;
  }
}
@media (max-width: 767px) {
  .hero__content {
    padding: 55px 0;
    display: block;
  }
}

.hero__content div:first-child {
  display: flex;
  align-items: center;
}
.hero__content div:last-child {
  padding: 73px 0;
}
@media (max-width: 1199px) {
  .hero__content div:last-child {
    padding: 39px 0 33px;
  }
}
@media (max-width: 767px) {
  .hero__content div:last-child {
    padding: 0;
    margin-left: auto;
    max-width: 70%;
  }
}

.hero__content ul {
  padding: 0;
  margin: 0;
  margin-top: 50px;
  list-style: none;
}
@media (max-width: 1199px) {
  .hero__content ul {
    margin-top: 25px;
  }
}
@media (max-width: 767px) {
  .hero__content ul {
    margin-top: 0;
    max-width: 115px;
    position: absolute;
    top: 47.5%;
    left: 15px;
  }
}

.hero__content li {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
}

.hero__content p {
  margin: 0;
  margin-bottom: 12px;
  padding: 0 51px;
  display: flex;
  justify-content: flex-end;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 42px;
  line-height: 58px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .hero__content p {
    padding: 0 5px;
    margin-bottom: 6px;
    font-size: 30px;
    line-height: 41px;
  }
}
@media (max-width: 767px) {
  .hero__content p {
    margin-bottom: 10px;
    font-size: 23px;
    line-height: 34px;
  }
}

.hero__content h1 {
  margin: 0;
  margin-bottom: 48px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 800;
  font-size: 91px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .hero__content h1 {
    margin-bottom: 39px;
    font-size: 72px;
    line-height: 86px;
    text-transform: lowercase;
    letter-spacing: 0.03em;
  }
}
@media (max-width: 767px) {
  .hero__content h1 {
    margin-bottom: 31px;
  }
}

.hero__image {
  max-height: 390px;
}

.hero__image img {
  transform: translateY(-41px);
  transition: all 200ms ease;
}
@media (max-width: 1199px) {
  .hero__image img {
    width: 324px;
    height: 100%;
    object-fit: cover;
    transform: translateY(-10px);
  }
}
@media (max-width: 767px) {
  .hero__image img {
    display: none;
  }
}

.hero__button {
  margin-left: auto;
  padding: 23px;
  display: block;
  width: 100%;
  max-width: 234px;
}
@media (max-width: 1199px) {
  .hero__button {
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .hero__button {
    margin-left: 0;
    padding: 24px;
    max-width: calc(100% - 30px);
    position: absolute;
    bottom: 79px;
    left: 15px;
  }
}

.light-button {
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-transform: uppercase;
  box-shadow: 0 0 0 4px currentColor inset;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (hover: hover) {
  .light-button:hover:not(.focus-visible) {
    box-shadow: 0 0 0 4px #e8e8e8 inset;
  }
}
.light-button.focus-visible:focus {
  box-shadow: 0 0 0 4px #e8e8e8 inset;
}

.dark-button {
  padding: 26px;
  color: #102654;
  text-transform: uppercase;
  box-shadow: 0 0 0 2px currentColor inset;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (hover: hover) {
  .dark-button:hover:not(.focus-visible) {
    box-shadow: none;
    background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
    color: #ffffff;
  }
}
.dark-button.focus-visible:focus {
  box-shadow: none;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  color: #ffffff;
}

.play-button {
  padding: 0;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: transparent;
  background-image: url("../img/svg/play-button-border.svg");
  cursor: pointer;
  border: none;
  border-radius: 50%;
}
.play-button::after {
  position: absolute;
  content: "";
  width: 72px;
  height: 72px;
  top: calc(50% - 36px);
  left: calc(50% - 36px);
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  border-radius: 50%;
}
@media (hover: hover) {
  .play-button:hover:not(.focus-visible)::after {
    background: linear-gradient(142.51deg, #d10a42 0%, #0041ca 87.33%);
  }
}
.play-button.focus-visible:focus::after {
  background: linear-gradient(142.51deg, #d10a42 0%, #0041ca 87.33%);
}

.play-button__triangle {
  margin-right: -3px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8.5px 0 8.5px 17px;
  border-color: transparent transparent transparent #ffffff;
  z-index: 5;
}

.gradient-button {
  padding: 26px;
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  cursor: pointer;
}
@media (hover: hover) {
  .gradient-button:hover:not(.focus-visible) {
    color: #102654;
    background: none;
    background-color: transparent;
    box-shadow: 0 0 0 2px currentColor inset;
  }
}
.gradient-button.focus-visible:focus {
  color: #102654;
  background: none;
  background-color: transparent;
  box-shadow: 0 0 0 2px currentColor inset;
}

.subscription-card {
  padding: 68px 77px;
  width: 100%;
  max-width: 442px;
  height: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 59px;
  color: #102654;
  background-color: #ffffff;
}
@media (hover: hover) {
  .subscription-card:hover:not(.focus-visible) {
    background: linear-gradient(142.51deg, #d10a42 0%, #0041ca 87.33%);
